<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
          {{ countProducts }} товара
        </span>
    </div>
    <div class="content__catalog">
      <aside class="filter">
        <h2 class="filter__title">Фильтры</h2>
        <product-filter :price-from="filter.priceFrom" :price-to="filter.priceTo" :color-data="colorData"
                        :category-id="filter.categoryId" v-on:filter-update="filterUpdate"/>

      </aside>
      <section class="catalog">
        <div v-if="productsLoading" class="loader">Загрузка товаров...</div>
        <div v-if="productsLoadingFailing" >Произошла ошибка при загрузке товаров...
          <button @click.prevent="loadProducts">обновить</button></div>
        <product-list :productList="products"/>
        <base-pagination :count="countProducts" :per-page="productsPerPage" v-model="page"/>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import ProductFilter from '@/components/ProductFilter.vue';
// import productList from '@/data/products';
import BasePagination from '@/components/BasePagination.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: {
    ProductList, BasePagination, ProductFilter,
  },
  data() {
    return {
      page: 1,
      productsPerPage: 3,
      filter: {
        priceFrom: 0,
        priceTo: 0,
        categoryId: 0,
        color: null,
      },
      productData: null,
      productsLoading: false,
      productsLoadingFailing: false,
      colorData: null,
    };
  },
  created() {
    this.loadProducts();
    this.loadColors();
  },
  watch: {
    page() {
      this.loadProducts();
    },
  },
  methods: {
    filterUpdate(event) {
      this.filter.priceFrom = event.priceFrom;
      this.filter.priceTo = event.priceTo;
      this.filter.categoryId = event.categoryId;
      this.filter.color = event.color;
      this.loadProducts();
      // this.$forceUpdate();
    },
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailing = false;
      // для того чтоб множество разне выполнялся один и тот же запрос
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filter.categoryId,
            minPrice: this.filter.priceFrom,
            maxPrice: this.filter.priceTo,
            colorId: this.filter.color,
          },
        })
          .then((response) => { this.productData = response.data; })
          .catch(() => { this.productsLoadingFailing = true; })
          .then(() => { this.productsLoading = false; });
      }, 0);
      // .then((response) => { this.productData = [new Set(response.data)]; });
    },
    loadColors() {
      axios.get(`${API_BASE_URL}/api/colors`)
        .then((response) => { this.colorData = response.data; });
    },
  },
  computed: {
    // нужно было, когда фильтрация была еа стороне JS(данные из файла)
    /* filteredProducts() {
      let resultList = productList;
      if (this.filter.priceFrom > 0) {
        resultList = resultList.filter((item) => item.price >= this.filter.priceFrom);
      }
      if (this.filter.priceTo > 0) {
        resultList = resultList.filter((item) => item.price <= this.filter.priceTo);
      }
      if (this.filter.categoryId !== 0) {
        resultList = resultList.filter((item) => item.categoryId === this.filter.categoryId);
      }
      if (this.filter.color) {
        resultList = resultList.filter((product) => product.colors != null
          && product.colors.includes(this.filter.color));
      }

      return resultList;
    }, */
    products() {
      // const offset = (this.page - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
      return this.productData ? this.productData.items.map((product) => ({
        ...product,
        src: product.image.file.url,
      })) : [];
    },
    /* colorList() {
      let resultList = [];
      productList.filter((product) => product.colors != null)
        .forEach((product) => { resultList = resultList.concat(product.colors); });
      resultList = [...new Set(resultList)];
      resultList.sort();
      console.log(resultList);
      return resultList;
    }, */
    countProducts() {
      // return this.filteredProducts.length;
      return this.productData ? this.productData.pagination.total : 0;
    },
  },
};
</script>

