<template>
  <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
    <fieldset class="form__block">
      <legend class="form__legend">Цена</legend>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="min-price" value="0"
               v-model.number="currentPriceFrom">
        <span class="form__value">От</span>
      </label>
      <label class="form__label form__label--price">
        <input class="form__input" type="text" name="max-price" value="12345"
               v-model.number="currentPriceTo">
        <span class="form__value">До</span>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Категория</legend>
      <label class="form__label form__label--select">
        <select class="form__select" name="category" v-model.number="currentCategoryId">
          <option value="0">Все категории</option>
          <option v-for="category in categoryList" :value="category.id" :key="category.id">
            {{ category.title }}
          </option>
        </select>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Цвет</legend>
      <ul class="colors">
        <li class="colors__item" v-for="filterColor in colorList" :key="filterColor.id">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" :value="filterColor.id"
                   v-model="currentColor">
            <span class="colors__value" :style="{'background-color': filterColor.code}">
                  </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Объем в ГБ</legend>
      <ul class="check-list">
        <li class="check-list__item">
          <label class="check-list__label">
            <input class="check-list__check sr-only" type="checkbox" name="volume" value="8"
                   checked="">
            <span class="check-list__desc">
                    8
                    <span>(313)</span>
                  </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label">
            <input class="check-list__check sr-only" type="checkbox" name="volume" value="16">
            <span class="check-list__desc">
                    16
                    <span>(461)</span>
                  </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label">
            <input class="check-list__check sr-only" type="checkbox" name="volume" value="32">
            <span class="check-list__desc">
                    32
                    <span>(313)</span>
                  </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label">
            <input class="check-list__check sr-only" type="checkbox" name="volume" value="64">
            <span class="check-list__desc">
                    64
                    <span>(313)</span>
                  </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label">
            <input class="check-list__check sr-only" type="checkbox" name="volume" value="128">
            <span class="check-list__desc">
                    128
                    <span>(313)</span>
                  </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label">
            <input class="check-list__check sr-only" type="checkbox" name="volume" value="264">
            <span class="check-list__desc">
                    264
                    <span>(313)</span>
                  </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <button class="filter__submit button button--primery" type="submit">
      Применить
    </button>
    <button class="filter__reset button button--second" type="button" @click="reset">
      Сбросить
    </button>
  </form>
</template>

<script>
// import categoryList from '../data/categories';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
// import colorList from '../data/colors';

export default {
  name: 'ProductFilter',
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorData'],
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColor: null,
      categoriesData: null,
    };
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    color(value) {
      this.currentColor = value;
    },
  },
  computed: {
    categoryList() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colorList() {
      // return colorList;
      return this.colorData ? this.colorData.items : [];
    },
  },
  methods: {
    submit() {
      const filterData = {
        categoryId: this.currentCategoryId,
        priceFrom: this.currentPriceFrom,
        priceTo: this.currentPriceTo,
        color: this.currentColor,
      };
      this.$emit('filter-update', filterData);
    },
    reset() {
      const filterData = {
        categoryId: 0,
        priceFrom: 0,
        priceTo: 0,
        color: null,
      };
      this.$emit('filter-update', filterData);
    },
    loadCategories() {
      axios.get(`${API_BASE_URL}/api/productCategories`)
        .then((response) => { this.categoriesData = response.data; });
    },
  },
  created() {
    this.loadCategories();
  },
};
</script>

