import Vue from 'vue';
import Vuex from 'vuex';
// import products from '@/data/products';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartProductsLoading: false,
    cartProductsLoadingFailing: false,
    orderInfo: null,
  },
  mutations: {
    /* старая мутация
    addProductToCart(state, { productId, amount }) {
      const product = state.cartProducts.find((item) => item.productId === productId);
      if (product) {
        product.amount += amount;
      } else {
        state.cartProducts.push({
          productId,
          amount,
        });
      }
    }, */
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const product = state.cartProducts.find((item) => item.productId === productId);
      if (product && amount > 0) {
        product.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },
    updateCartProductsLoading(state, productsLoading) {
      state.productsLoading = productsLoading;
    },
    updateCartProductsLoadingFailing(state, productsLoadingFailing) {
      state.productsLoadingFailing = productsLoadingFailing;
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = this.state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
  },
  getters: {
    /* cartDetailsProducts(state) {
      return state.cartProducts.map((item) => {
        return {
          ...item,
          product: products.find((p) => p.id === item.productId),
        };
      });
    }, */
    cartDetailsProducts(state) {
      return state.cartProducts.map((item) => {
        const prod = state.cartProductsData.find((p) => p.product.id === item.productId).product;
        if (!prod) {
          return null;
        }
        const product = {
          productId: item.productId,
          amount: item.amount,
          product: {
            ...prod,
            img: prod.image.file.url,
          },
        };
        return product;
      });
    },
    cartProductsLoading(state) {
      return state.cartProductsLoading;
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailsProducts.reduce((acc, item) => item.product.price * item.amount + acc, 0);
    },
    cartProductCount(state) {
      return state.cartProducts.length;
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      context.commit('updateCartProductsLoading', true);
      context.commit('updateCartProductsLoadingFailing', false);
      return axios.get(`${API_BASE_URL}/api/baskets`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductData', response.data.items);
          context.commit('syncCartProducts');
        })
        .catch(() => { context.commit('updateCartProductsLoadingFailing', true); })
        .then(() => { context.commit('updateCartProductsLoading', false); });
    },
    addProductToCart(context, { productId, amount }) {
      context.commit('updateCartProductsLoading', true);
      context.commit('updateCartProductsLoadingFailing', false);
      return new Promise((resolve) => { setTimeout(resolve, 0); })
        .then(() => axios.post(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
          .then((response) => {
            context.commit('updateCartProductData', response.data.items);
            context.commit('syncCartProducts');
          }));
      // .catch(() => { context.commit('updateCartProductsLoadingFailing', true); })
      // .then(() => { context.commit('updateCartProductsLoading', false); });
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });

      if (amount < 1) {
        return new Promise();
      }
      return new Promise((resolve) => { setTimeout(resolve, 0); })
        .then(() => axios.put(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
          .then((response) => {
            context.commit('updateCartProductData', response.data.items);
          }))
        .catch(() => {
          context.commit('syncCartProducts');// в случае ошибки - возвращаем старое значение
        });
    },
    deleteCartProduct(context, productId) {
      context.commit('deleteCartProduct', productId);

      return new Promise((resolve) => { setTimeout(resolve, 0); })
        .then(() => axios.delete(`${API_BASE_URL}/api/baskets/products`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            productId,
          },
        }))
        .catch(() => {
          context.commit('syncCartProducts');// в случае ошибки - возвращаем старое значение
        });
    },
  },
});
