<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#"  class="pagination__link pagination__link--arrow" @click.prevent="paginate(page-1)"
         aria-label="Предыдущая страница" :class="{'pagination__link--disabled' : page <= 1}">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNum in pages" v-bind:key="pageNum">
      <a href="#" class="pagination__link" :class="{'pagination__link--current' : pageNum == page}"
         @click.prevent="paginate(pageNum)">
        {{ pageNum }}
      </a>
    </li>
    <li class="pagination__item">
      <a href="#"  class="pagination__link pagination__link--arrow" aria-label="Следующая страница"
         @click.prevent="paginate(page+1)" :class="{'pagination__link--disabled' : page >= pages}">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
// import productList from '../data/products';
// import ProductItem from './ProductItem.vue';

export default {
  name: 'BasePagination',
  props: ['page', 'count', 'perPage'],
  model: {
    prop: 'page',
    event: 'paginate',
  },
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
  },
};
</script>

