<template>
  <li class="cart__item product">

    <div class="product__pic">
      <img :src="item.product.img" width="120" height="120"
           :srcset=item.product.srcset alt="Название товара">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <!--            <p class="product__info">
                  Объем: <span>128GB</span>
                </p> -->
    <span class="product__code">
                Артикул: {{ item.productId }}
              </span>

    <cart-item-counter v-bind:itemAmount="item.amount"
                       v-on:counter-update="amount = $event"/>

    <b class="product__price">
      {{ item.product.price * item.amount | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
            @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>

  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import CartItemCounter from '@/components/CartItemCounter.vue';

export default {
  filters: { numberFormat },
  components: { CartItemCounter },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteCartProduct', updateProductAmount: 'updateCartProductAmount' }),
    /* deleteProduct(productId) {
      this.$store.commit('deleteProduct', { productId: productId });
    }, */
  },
};
</script>

